.container{position:absolute;top:100%;width:200px;font-size:var(--juno-generic-font-normal);max-height:0;margin-top:8px;overflow:hidden;border-radius:4px;opacity:0;transform:translateY(-15px);transition-property:opacity, transform;transition-duration:0.3s}.withShadow{box-shadow:0 3px 8px 0 rgba(154,154,154,0.7)}.container.right{left:0}.container.show{max-height:1000px;opacity:1;transform:translateY(0);z-index:2}.container.outbound,.container.left{left:auto;right:0}.item{cursor:pointer;user-select:none;padding:9px 20px;color:var(--juno-color-secondary);background:var(--juno-color-primary)}.item:hover,.item:active{background:var(--juno-color-primary-dark)}.item.secondary{color:var(--juno-color-neutral-dark);background:var(--juno-color-secondary)}.item.secondary:hover,.item.secondary:active{background:var(--juno-color-secondary-dark)}
