.sidemenu{background:var(--juno-color-secondary);box-shadow:0 1px 2px 0 rgba(0,0,0,0.2);position:relative;font-size:var(--juno-generic-font-normal);left:-var(--juno-generic-layout-margin);top:-var(--juno-generic-layout-margin);width:180px;max-height:100%}.list{position:sticky;top:0;z-index:3;padding:25px 0}.item{overflow:hidden}.link{display:block;position:relative;padding:5px 0 5px 20px;border-left:4px solid transparent;line-height:30px;color:var(--juno-color-neutral);cursor:pointer;transition-property:color, background;transition-duration:var(--juno-generic-transition-short)}.link .iconExpand{position:absolute;top:16px;right:20px;width:5px;height:5px;border-left:1px solid var(--juno-color-neutral-dark);border-top:1px solid var(--juno-color-neutral-dark);transform:rotate(225deg);transition:transform var(--juno-generic-transition-short)}.link:hover{color:var(--juno-color-primary)}.linkActive{cursor:default;color:var(--juno-color-primary);font-weight:500}.linkActive .iconExpand{top:18px;transform:rotate(45deg)}.linkActive:not(.withInnerMenu){border-color:var(--juno-color-primary);background:var(--juno-color-secondary-dark)}.linkActive.withInnerMenu{margin-bottom:8px;font-weight:bold}.innerMenu{max-height:0;overflow:hidden}.linkActive+.innerMenu{max-height:1000px}.innerMenu .link{display:block;padding-left:40px;line-height:30px}
