.button{display:inline-block;box-sizing:border-box;height:32px;padding:0 12px;border-radius:4px;border:none;font-size:var(--juno-generic-font-normal);font-weight:500;line-height:32px;text-align:center;background-color:var(--juno-color-primary);color:var(--juno-color-secondary);outline:none;cursor:pointer;transition-duration:var(--juno-generic-transition-short);transition-property:background color}.small{min-width:90px;height:24px;font-size:var(--juno-generic-font-small);line-height:24px;padding:0 10px}.default:hover,.default:active{background-color:var(--juno-color-primary-dark)}.success{background-color:var(--juno-color-success)}.success:hover,.success:active{background-color:var(--juno-color-success-dark)}.alert{background-color:var(--juno-color-danger)}.alert:hover,.alert:active{background-color:var(--juno-color-danger-dark)}.warning{background-color:var(--juno-color-warning)}.warning:hover,.warning:active{background-color:var(--juno-color-warning-dark)}.secondary{background-color:var(--juno-color-secondary);color:var(--juno-color-neutral-dark);border:solid 1px var(--juno-color-neutral-light);transition-property:border}.secondary:hover,.secondary:active{border-color:var(--juno-color-primary)}.fullWidth{width:100%}.minWidth{min-width:100px}.disabled,.button[disabled]{cursor:default;border:none;background-color:var(--juno-color-disabled);color:var(--juno-color-secondary)}.flat{border-radius:0}.buttonGroup .button:not(:last-child){margin-right:20px}
