.label{display:inline-block;box-sizing:border-box;height:20px;padding:4px 10px 0;border-radius:20px;font-size:var(--juno-generic-font-small);line-height:1;font-weight:bold;white-space:nowrap;vertical-align:middle;text-transform:capitalize;text-align:center;color:#fff;background-color:var(--juno-color-primary)}.label:not(:last-child){margin-right:5px}.unavailable{background-color:var(--juno-color-neutral-light)}.success{background-color:var(--juno-color-success)}.alert{background-color:var(--juno-color-danger)}.warning{background-color:var(--juno-color-warning)}.error{color:var(--juno-color-danger);background-color:var(--juno-color-danger-light)}.offset{position:relative;top:-2px;margin:0 8px;text-transform:capitalize}
