.actionButton{position:relative;transition-duration:var(--juno-generic-transition-short);transition-property:opacity background}.icon{position:absolute;top:50%;left:50%;width:26px;height:26px;margin-top:-13px;margin-left:-13px;transition:opacity var(--juno-generic-transition-short);opacity:0}.pending .iconPending,.success .iconSuccess,.error .iconError{opacity:1}.iconPending{width:16px;height:16px;margin-top:-8px;margin-left:-8px;stroke:var(--juno-color-secondary)}.text{opacity:1;transition:opacity var(--juno-generic-transition-short)}.pending .text,.success .text,.error .text{opacity:0}.error{background-color:var(--juno-color-danger)}.error:hover,.error:active{background-color:var(--juno-color-danger-dark)}
