.item{display:flex;user-select:none;cursor:pointer;top:0}.item{margin:0 10px 10px 0}.item:last-child{margin-right:0}.inputContainer{box-sizing:border-box;width:20px;height:20px;position:relative;border:1px solid var(--juno-color-neutral-light);background:var(--juno-generic-color-white);border-radius:2px}.inputContainer.checked{background:var(--juno-color-primary) url("./check.svg") no-repeat center center;border:none}.input{opacity:0;z-index:1;cursor:pointer}.label{height:20px;line-height:20px;margin-left:8px;font-size:var(--juno-generic-font-normal);color:var(--juno-color-neutral)}.invalid{color:var(--juno-color-danger)}.item.disabled{color:var(--juno-color-disabled);cursor:default}.item.disabled .inputContainer{opacity:0.7;background:var(--juno-color-disabled) none;border:1px solid var(--juno-color-neutral-light)}.item.disabled .input{cursor:default}
