.toggle{position:relative;height:32px;user-select:none}.toggleCheckbox{position:absolute;left:0;top:0;opacity:0}.toggleLabel{display:block;position:relative;width:74px;height:var(--juno-generic-toggle-height);border-radius:var(--juno-generic-toggle-height);cursor:pointer;background:var(--juno-color-danger)}.toggleLabel:after{box-sizing:border-box;content:'';position:absolute;top:0;left:-1px;display:block;width:var(--juno-generic-toggle-height);height:var(--juno-generic-toggle-height);border:solid 1px #ccd1d9;background-color:var(--juno-generic-color-white);border-radius:var(--juno-generic-toggle-height);box-shadow:2px 0 0 0 rgba(0,0,0,0.1);transition:all var(--juno-generic-transition-short) ease-in-out}.toggleLabel:before{content:'No';display:block;position:absolute;left:auto;right:14px;top:9px;text-transform:uppercase;text-align:center;font-size:var(--juno-generic-font-small);font-weight:bold;color:var(--juno-generic-color-white);line-height:16px}.toggleLabel.checked{border:1px solid var(--juno-color-success);background:var(--juno-color-success)}.toggleLabel.checked:before{content:'Yes';right:auto;left:10px;color:var(--juno-generic-color-white)}.toggleLabel.checked:after{left:45px;box-shadow:-2px 0 0 0 rgba(0,0,0,0.1)}
