.wrapper{display:flex;flex-wrap:nowrap;flex-direction:column;position:relative;color:var(--juno-color-neutral);width:100%}.input{position:relative;height:28px;line-height:28px;border:none;outline:none;color:var(--juno-color-neutral-dark);font-size:var(--juno-generic-font-large);font-weight:var(--juno-font-weight-medium);border-bottom:1px solid var(--juno-color-neutral-light);padding:0 30px 0 0;cursor:pointer;user-select:none}.input:after{content:'';display:block;position:absolute;top:14px;right:8px;z-index:2;width:0;height:0;border-top:4px solid var(--juno-color-neutral-light);border-left:4px solid transparent;border-right:4px solid transparent;pointer-events:none}.options{position:absolute;top:30px;left:0;border-radius:3px;box-shadow:0 1px 4px 0 rgba(154,154,154,0.5);background-color:var(--juno-color-secondary);padding:7px 16px;box-sizing:border-box;display:flex;flex-direction:column;z-index:10;min-width:180px}.option{width:100%;font-size:var(--juno-generic-font-normal);min-height:24px;line-height:24px;border:none;background:none;padding:0;outline:none;margin-bottom:7px;text-align:left;cursor:pointer;white-space:nowrap}.option:last-child{margin:0}.selected{font-weight:var(--juno-font-weight-medium)}.placeholder{color:var(--juno-color-neutral-light)}.placeholder.input{border-color:var(--juno-color-neutral-light)}.disabled{cursor:not-allowed}
