@keyframes copyBtnClicked{from{transform:scale(1)}to{transform:scale(1.5);opacity:0.01}}.quickCopy{position:relative}.quickCopy:hover{opacity:1}.quickCopy:hover .iconContainer{opacity:0.6}.iconContainer{position:absolute;top:-6px;right:-30px;width:30px;display:flex;justify-content:flex-end;transition:opacity 0.15s ease;cursor:pointer;opacity:0.01}.clicked{animation-name:copyBtnClicked;animation-duration:0.2s;animation-iteration-count:1;animation-timing-function:ease;animation-direction:alternate}.hidden{position:absolute;left:-500px;top:0;width:0;height:0;z-index:-1}
