:root {
  --juno-generic-color-secondary-grey: #e2e6f4;
  --juno-generic-color-secondary-grey-darker: #d6dae7;

  --juno-generic-color-primary-grey: #879bab;
  --juno-generic-color-primary-grey-lighter: rgba(135, 155, 171, 0.6);
  --juno-generic-color-primary-grey-lighter-2: rgba(135, 155, 171, 0.4);

  --juno-generic-color-background: #f7f8fc;

  --juno-generic-color-primary-blue: #0f375a;
  --juno-generic-color-white: #ffffff;
  --juno-generic-color-green: #3ec07f;
  --juno-generic-color-red: #ec5555;
  --juno-generic-color-orange: #ffa848;
  --juno-generic-color-electric: #6e8aff;

  --juno-generic-color-purple: #44547f;
  --juno-generic-color-purple-lighter: #4e5b7f;
  --juno-generic-color-purple-darker: #323e5e;

  --juno-generic-input-height: 28px;
  --juno-generic-toggle-height: 32px;

  --juno-generic-card-padding: 24px;
  --juno-generic-layout-margin: 16px;

  --juno-generic-transition-short: 0.15s;
  --juno-generic-transition-medium: 0.3s;

  /* Fonts */
  --juno-generic-font-family: 'Helvetica Neue', Helvetica, Arial, sans-serif;
  --juno-generic-font-small: 12px;
  --juno-generic-font-normal: 14px;
  --juno-generic-font-large: 16px;

  --juno-generic-font-small-wide-screen: 13px;
  --juno-generic-font-normal-wide-screen: 16px;
  --juno-generic-font-large-wide-screen: 18px;

  /* TODO: clean up redundant line-heights */
  --juno-generic-line-height-normal: 1.3;
  --juno-generic-line-height-medium: 1.25;
  --juno-generic-line-height-small: 1.22;
  --juno-generic-line-height-xsmall: 1.12;

  --juno-timeline-label-width: 96px;
  --juno-action-button-width: 95px;

  /* 1.5 colors */
  --juno-base-color-body-background-color: rgb(240, 240, 241, 0.6);
  --juno-base-color-primary-blue: #28509f;
  --juno-base-color-primary-blue-dark: #1c3870;
  --juno-base-color-dark-sky-blue: #4a8df5;
  --juno-base-color-green-mint: #3ec07f;
  --juno-base-color-green-dark: #1bae64;
  --juno-base-color-red: #ec4949;
  --juno-base-color-red-dark: #b72626;
  --juno-base-color-orange: #ffb347;
  --juno-base-color-orange-dark: #ef981d;
  --juno-base-color-tender-black: #23282e;
  --juno-base-color-steel-grey: #5b5f65;
  --juno-base-color-slate-grey: #919498;
  --juno-base-color-silver-grey: #b1b4ba;
  --juno-base-color-fog-grey: #f0f0f1;
  --juno-base-color-yellow-light: #fdf9c8;
  --juno-base-color-red-light: #ffe0e0;
  --juno-base-color-white: #ffffff;

  --juno-color-primary: var(--juno-base-color-primary-blue);
  --juno-color-primary-dark: var(--juno-base-color-primary-blue-dark);
  --juno-color-primary-light: var(--juno-base-color-dark-sky-blue);
  --juno-color-secondary: var(--juno-base-color-white);
  --juno-color-secondary-dark: var(--juno-base-color-fog-grey);
  --juno-color-success: var(--juno-base-color-green-mint);
  --juno-color-success-dark: var(--juno-base-color-green-dark);
  --juno-color-danger: var(--juno-base-color-red);
  --juno-color-danger-dark: var(--juno-base-color-red-dark);
  --juno-color-danger-light: var(--juno-base-color-red-light);
  --juno-color-warning: var(--juno-base-color-orange);
  --juno-color-warning-dark: var(--juno-base-color-orange-dark);
  --juno-color-warning-light: var(--juno-base-color-yellow-light);
  --juno-color-disabled: var(--juno-base-color-silver-grey);
  --juno-color-neutral-light: var(--juno-base-color-slate-grey);
  --juno-color-neutral: var(--juno-base-color-steel-grey);
  --juno-color-neutral-dark: var(--juno-base-color-tender-black);

  --juno-font-weight-medium: 500;
}
