.input{box-sizing:border-box;width:100%;height:var(--juno-generic-input-height);min-height:var(--juno-generic-input-height);padding:0 12px 0 0;border:none;border-bottom:1px solid var(--juno-color-neutral-light);border-radius:0;font-family:inherit;font-size:var(--juno-generic-font-large);font-weight:var(--juno-font-weight-medium);line-height:var(--juno-generic-input-height);color:var(--juno-color-neutral-dark);background:transparent;-webkit-appearance:none;-moz-appearance:none}.input:focus{border-bottom:1px solid var(--juno-color-primary);outline:none;position:relative;z-index:2}.textarea{height:inherit}.noPadding{padding:0}.noBorder{border:none}.invalid,.invalid:focus{z-index:3 !important;border-color:var(--juno-color-danger) !important;border-bottom-style:solid !important}.invalid:focus,.invalid:active{box-shadow:0 1px 0 0 var(--juno-color-danger) !important}@keyframes autofill-outline{0%{box-shadow:0 2px 0 0 transparent}20%{border-color:transparent}30%{box-shadow:0 2px 0 0 var(--juno-generic-color-electric)}70%{box-shadow:0 2px 0 0 var(--juno-generic-color-electric)}80%{border-color:transparent}90%{box-shadow:0 2px 0 0 transparent}100%{border-color:var(--juno-color-neutral-light)}}.autofilled{animation:autofill-outline 2.5s}.withArrow:after{content:'';display:block;position:absolute;top:14px;right:8px;z-index:2;width:0;height:0;border-top:4px solid var(--juno-generic-color-primary-blue);border-left:4px solid transparent;border-right:4px solid transparent;pointer-events:none}
