/*  Custom Styles  */
.react-tel-input:after {
  content: '';
  display: block;
  position: absolute;
  top: 14px;
  right: 8px;
  z-index: 2;
  width: 0;
  height: 0;
  border-top: 4px solid #0f375a;
  border-left: 4px solid transparent;
  border-right: 4px solid transparent;
  pointer-events: none;
}

.react-tel-input input[type='text'],
.react-tel-input input[type='tel'] {
  box-sizing: border-box;
  width: 100%;
  height: var(--juno-generic-input-height);
  padding: 0 12px 0 0;
  border: none;
  border-bottom: 1px solid var(--juno-color-neutral-light);
  border-radius: 0;
  font-family: inherit;
  font-size: var(--juno-generic-font-large);
  color: var(--juno-color-neutral-dark);
  background: transparent;
  -webkit-appearance: none;
  -moz-appearance: none;
  box-shadow: none;
}

.react-tel-input input[type='text']:focus,
.react-tel-input input[type='tel']:focus {
  border-bottom: 1px solid var(--juno-color-primary);
  outline: none;
  position: relative;
  z-index: 2;
}

.react-tel-input .flag-dropdown {
  right: 0;
  bottom: 1px;
  /* z-index should be equal to z-index from form-item-input-focused mixin */
  z-index: 2;
  background-color: var(--juno-generic-color-white);
  border: none;
}

.react-tel-input .flag-dropdown:hover .selected-flag,
.react-tel-input .flag-dropdown.open-dropdown .selected-flag {
  background-color: transparent;
}

.react-tel-input .selected-flag {
  /* z-index should be equal to z-index from with-arrow mixin */
  z-index: 2;
  height: var(--juno-generic-input-height);
  padding-right: 38px;
}

.react-tel-input .selected-flag .arrow {
  /* hide original arrow in favor with-arrow mixin */
  display: none;
}

.react-tel-input .country-list {
  top: var(--juno-generic-input-height);
}
