.header{display:flex;flex-direction:row;flex-wrap:nowrap;flex-shrink:0;height:70px;background:var(--juno-color-primary);color:var(--juno-color-secondary);box-shadow:0 1px 2px 0 rgba(0,0,0,0.2);line-height:70px;z-index:3;font-size:var(--juno-generic-font-small);transform:translateZ(0)}.sticky{position:sticky;top:0;z-index:4}.logo{width:180px;padding-left:24px;color:var(--juno-color-secondary);line-height:72px}.img{width:50px;height:13px;margin-right:10px;vertical-align:baseline;fill:var(--juno-color-secondary)}.title{color:var(--juno-color-secondary)}.sectionLink{display:inline-block;padding:24px 25px 22px;line-height:24px;color:var(--juno-color-secondary);transition:background var(--juno-generic-transition-short)}.sectionLink:hover{background:var(--juno-color-neutral-light);color:var(--juno-color-secondary)}.sectionLinkActive{transition:background var(--juno-generic-transition-medium)}.sectionLinkActive,.sectionLinkActive:hover{background:var(--juno-color-primary-dark);color:var(--juno-color-secondary);cursor:default}.label{margin-left:5px}.user{max-width:230px;margin-left:auto;overflow:hidden;white-space:nowrap;text-overflow:ellipsis}.button{display:block;height:70px;margin:0 0 0 20px;padding:0 20px;font-weight:400;background-color:var(--juno-color-neutral);font-size:var(--juno-generic-font-small);color:var(--juno-color-secondary)}.button:hover{background-color:var(--juno-color-neutral-light)}
