.container{display:flex;flex-direction:column}.calendarContainer{position:relative}.calendar{position:absolute;top:1px;background-color:white;z-index:5;border-radius:4px;box-shadow:0 1px 2px 0 rgba(0,0,0,0.2)}.calendar :global(.DayPicker-Weekday){font-size:16px;line-height:1.12;font-weight:500;color:var(--juno-generic-color-primary-grey)}.calendar :global(.DayPicker-Week):nth-of-type(odd){background-color:rgba(135,155,171,0.07)}.calendar :global(.DayPicker-Day){border-radius:0}
